<template>
  <!-- 积尘分析 -->
  <div class="boss_div">
    <div class="top_box">
      <div class="top_center">
        <button @click="detailClk('1')" :class="modelShow==1 ? 'active' : '' ">城市道路</button>
        <button @click="detailClk('2')" :class="modelShow==2 ? 'active' : '' ">公路</button>
        <button @click="detailClk('3')" :class="modelShow==3 ? 'active' : '' ">乡镇道路</button>
        <!-- <button @click="detailClk('4')" :class="modelShow==4 ? 'active' : '' ">积尘负荷排名</button> -->
      </div>
    </div>
    <div class="content_div">
      <div class="table-box">
        <div class="my-table-header">
          <span style="width: 200px">序号</span>
          <span style="width: 200px">道路名称</span>
          <span style="width: 200px">街道名称</span>
          <span style="width: 200px">积尘负荷</span>
        </div>
        <div class="my-table-body">
          <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">
            <a href="javascript:;" @click="goToDetails(item)" v-for="(item, index) in tableData" :key="index" class="my-table-content">
              <div style="width: 200px">{{ item.id }}</div>
              <div style="width: 200px">{{ item.road_name }}</div>
              <div style="width: 200px">{{ item.town_name }}</div>
              <div style="width: 200px">{{ item.dust_load }}</div>
            </a>
          </vue-seamless-scroll>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { http } from "@/utils/request";
export default {
  data() {
    return {
      modelShow: "1",
      titleData: "",
      tabeName: "1",
      // 表格数据
      tableData: [],
      time: this.$moment().subtract(1, "days").format("YYYY-MM-DD 00:00:00"),
      // list:[{name:'国考断面'}, {name:'市考断面'}, {name:'社区断面'} ]
    };
  },
  created() {
    this.getAirsDataList();
  },
  methods: {
    detailClk(val) {
      this.modelShow = val;
      this.tabeName = val;
      this.getAirsDataList();
    },

    getAirsDataList() {
      let json = {
        project_id: "9",
        start_time: "2021-03-01 00:00:00",
        end_time: "2021-03-01 00:00:00",
        road_type: this.tabeName,
      };
      http
        .get(
          "https://api-road-dust-service.airqualitychina.cn:9998/api/v2/road-dust-pipeline",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          let resData = res.data.result.data;
          if (res.data.code === 2000 && !!resData) {
            this.tableData = resData.map((it) => {
              for (const key in it) {
                if (Object.hasOwnProperty.call(it, key)) {
                  it[key] = !!it[key] ? it[key] : "";
                }
              }
              return it;
            });
          }
        });
    },
  },
  watch: {
    titleData(val) {
      // console.log(val);
    },
  },
};
</script>

<style lang="scss" scoped>
.boss_div {
  width: 1919px;
  height: 710px;
  // border: 1px solid #49abfa;
  background: url("../../assets/dbsdmfx-bg.png") 0 no-repeat;
  background-size: 100% 100%;
  padding: 24px 0 0 0;
  .top_box {
    width: 1919px;
    height: 125px;
    .top_center {
      // width: 1300px;
      // margin: 0 auto;
      display: flex;
      justify-content: center;
      button {
        margin-top: 40px;
        width: 302px;
        height: 61px;
        margin-right: 33px;
        font-size: 30px;
        background: url("../../assets/button-bg.png") 0 no-repeat;
        color: #fff;
        border: none;
        cursor: pointer;
        &.active {
          background: url("../../assets/button-active-bg.png") 0 no-repeat;
        }
      }
    }
  }
  .content_div {
    width: 1919px;
    height: 500px;
    .table-box {
      .my-table-header {
        color: #fff;
        width: 1919px;
        height: 66px;
        line-height: 66px;
        background: linear-gradient(
          to right,
          rgba(74, 129, 169, 0) 0%,
          rgba(65, 151, 227, 0.75) 50%,
          rgba(74, 129, 169, 0) 100%
        );
      }
      .my-table-body {
        max-height: 415px;
        overflow: hidden;
        font-size: 21px;
        .my-table-content {
          height: 74px;
          line-height: 74px;
          opacity: 1;
          .text-red,
          .text-green {
            display: inline-block;
            width: 120px;
          }
        }
        .my-table-content:nth-child(2n + 1) {
          background: none;
        }
        .my-table-content:nth-child(2n) {
          background: linear-gradient(
            to right,
            rgba(74, 129, 169, 0) 0%,
            #447ca5 50%,
            rgba(74, 129, 169, 0) 100%
          );
        }
      }
    }
  }
}
</style>